@import url(fonts.css);

.menu-button {
	position: fixed;
	top: 40px;
	right: 60px;
	height: 60px;
	width: 60px;
	cursor: pointer;
	z-index: 100;
}

.menu-frame {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('../assets/img/menu_frame_black.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.5s;
}

.menu-button.open .menu-frame {
  opacity: 0;
}

.menu-bar {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30%;
  height: 1px;
  background-color: #0c0c0c;
  transition: all 0.5s;
}

.menu-bar.bar1 {
  top: 45%;
}

.menu-bar.bar2 {
  top: 55%;
}

.menu-button.open .menu-bar {
  top: 50%;
  left: 25%;
  width: 50%;
  opacity: 0.7;
  height: 1.5px;
  background-color: #f3f3f3;
}

.menu-button.open .menu-bar.bar1 {
  transform: rotate(-35deg);
}

.menu-button.open .menu-bar.bar2 {
  transform: rotate(35deg);
}

.menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 25%;
  height: 100vh;
  background-color: rgb(133, 129, 136);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  transform: translateX(100%);
  transition: all 0.5s ease-in-out;
}

.menu.open {
  transform: translateX(0);
}

.menu-list {
  height: 80%;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-item {
  font-family: 'Accia Piano';
  font-size: 1.5rem;
  color: #f3f3f3;
  letter-spacing: 0.3rem;
  margin-top: 5vh;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.menu-item.active, .menu-item:hover {
  color: #fcf0cc;
}
